<template>
  <div class="loginPage">
    <h1 class="loginTitle">魔三宠+管理后台</h1>
    <Card class="loginPanel" title="用户登陆" icon="user">
      <i-form label-position="top" autocomplete="on" :model="form">
        <FormItem label="用户名" required label-for="username" @submit.native.prevent>
          <Input
            prefix="ios-contact"
            id="username"
            v-model="form.username"
            placeholder="请输入用户名"
            clearable
            autofocus
          />
        </FormItem>
        <FormItem label="密码" required label-for="password">
          <Input
            prefix="ios-contact"
            id="password"
            type="password"
            v-model="form.password"
            clearable
            placeholder="请输入密码"
          />
        </FormItem>
        <Button type="success" long v-on:click="login">登陆</Button>
      </i-form>
    </Card>
  </div>
</template>

<script>
const storageApi = require("@helpers/storage.js");
export default {
  data() {
    return {
      form: {
        username: null,
        password: null
      }
    };
  },
  methods: {
    login() {
      if (!this.form.username) {
        this.$Notice.error({
          title: "输入错误",
          desc: "您没有输入用户名，请输入用户名后再登陆"
        });
        return;
      }
      if (!this.form.password) {
        this.$Notice.error({
          title: "输入错误",
          desc: "您没有输入密码，请输入密码后再登陆"
        });
        return;
      }
      // 用户登陆逻辑
      // 获取到 token
      let token = "abc";
      // token 放入 localstoage
      storageApi.setStor("token", token);
      // 设置 vuex 的数据
      this.$store.commit("login", token);
      // 检测是否有 param，如果有，跳转到 to 去
      // 暂时直接进 Home ，后续处理这个问题
      this.$router.replace({
        name: "home"
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
