  background-color: #282a36;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.loginTitle {
  color: #f8f8f2;
}

.loginPanel {
  width: 30em;
}
</style>
